@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  font-family: "Poppins", sans-serif;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 200;
  font-size: 3rem;
  color: rgb(248, 255, 218);
}

h2 {
  font-family: "Poppins", sans-serif;
  font-size:  3vh;
  font-weight: 300;
}

h3 {
  font-weight: 400;
  text-align: center;
}

.header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: rgb(131, 207, 44);
  height: 5vh;
}

.logo {
  height: 5vh;
  width: auto;
}

.message p {
  font-size: 3rem;

  &.info {
    font-size: 1rem;
  }
}

.message-content {
  display: flex;
  align-items: center;
}

.site-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 5vh;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.transcript-logger {
  position: fixed;
  top: 0;
  right: 0;
  width: 30%;
  height: 100%;
  border-radius: 20px;
  height: 50vh;
  background-color: #8eff317e;
  overflow: auto;
  padding: 20px;
}

.firefleye-button {
  margin-left: 22vw;
  font-family: "Poppins", sans-serif;
  border-radius: 30rem;
  background-color: rgb(26, 146, 42);
  padding: 0.7em;
  border: none;
  color: white;
  box-shadow: 0px 2px 10px 0px rgba(11, 70, 67, 0.5);
  margin: 0.1rem;
  transition: all 0.3s cubic-bezier(1, -0.01, 0, 1.22);
  font-weight: 700;

  &:hover {
    transform: scale(1.3);
    background-color: rgb(3, 54, 26);
    color: white;
  }
}

.transcript-container {
  position: fixed;

  gap: 10px;
}

.lang-select {
  font-family: "Poppins", sans-serif;
  border: none;
  border-radius: 20em;
  padding: 1rem;
  margin: 0.1rem;
}
