.button-container {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 2%;
}

.feedback-button-options {
  border-radius: 3rem;
  margin: 0.5em;
  max-height: 60vh;
  box-shadow: 0 5px 5px 1px #0b083b59;
}

.fleyecons {
  width: auto;
  max-width: 10vw;
  height: auto;
  max-height: 30vh;
}

.feedback-button {
  width: auto;
  height: 2.5vw;

  border-radius: 50%;
  background-color: transparent;
  border: none;
  transition: transform 0.1s ease-in-out;

  &:hover {
    transform: scale(1.2);
  }
}

.button-look {
  font-family: "Poppins", sans-serif;
  border: none;
  font-size: 2vw;
  color: white;
  background-color: rgba(255, 255, 255, 0);
  transition: background-color 0.3s ease-in-out;
  border-radius: 1em;
  margin: 1rem;
  transition: all 0.1s ease-in-out;
  box-shadow: 0 5px 5px 1px #0b083b59;
  &:hover {
    transform: scale(1.2);
    background-color: black;
  }
}

.feedback-notification {
  position: fixed;
  top: 50vh;
  left: 5vw;

  background-color: rgba(0, 128, 0, 0.8);
  color: white;
  padding: 1vw 2vh;
  border-radius: 20rem;
  font-size: 18px;
  z-index: 1000;
  opacity: 0;
  animation: pop-in 0.3s cubic-bezier(1, -0.01, 0, 1.22) forwards;
}

@keyframes pop-in {
  0% {
    transform: scale(0.5, 2);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
