

.info-card {
    position: fixed;
    max-width: 80vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    height: 70vh;
    border-radius: 3rem;
    overflow-y: scroll;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
    z-index: 9999;
  }
  

  
  .info-card img {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
    border-radius: 1em;
  }
  
  .info-card p {
    font-size: 16px;
    line-height: 1.5;
    color: #333;
  }
  

  .info-img{
    align-content: center;
    display: flex;
    width: 5vw;
  }